import React from 'react';
import AbstractList from '../Core/AbstractList'
import moment from 'jalali-moment'


const ButtonStyle = {
    marginLeft: "10px"
}

export default class AllOrderList extends AbstractList {


	getEntityName() {
		return "allOrder";
	}

	getSearchPanel() {
		return [
			{ id: "EQ:title", type: 'text', label: '' },
			{ id: "EQ:goods.id", type: 'combo', label: '',
				values: this.state.options ? this.state.options.products : [],
				convertToVal: it => it.id, convertToStr: it => it.title },
			{ id: "EQ:currentState.id", type: 'combo', label: '',
				values: this.state.options ? this.state.options.states : [],
				convertToVal: it => it.id, convertToStr: it => it.title }
		]
	}

	getTableHead() {
		return ["", "" , " ", "", " ", "", ""];
	}

	createAble() {
        return false;
	}

	gotToNextState(id) {
		this.rest.postCustom(id + "/nextState")
			.then( resp => this.doSearch() );
	}

	convertToTableRow( data ) {
		let price = 0;
		data.items.forEach( item => price += item.price );
		return (
			<tr key={"item_" + data.id}>
				<td>{data.id}</td>
				<td>{data.title}</td>
				<td>{moment(data.createDate).locale('fa').format('YYYY/MM/DD')}</td>
				<td>{data.currentState ? data.currentState.title : ""}</td>
				<td>{data.creator ? data.creator.username : ""}</td>
				<td>{price}</td>
				<td><a style={ButtonStyle} href="javascript:void(0);" onClick={this.gotToNextState.bind(this, data.id)}><i className="fa fa-angle-right" aria-hidden="true"></i></a></td>
			</tr>
		);
	}

}



